//apiapi
// api

//var root = 'http://www.ryan1h.top/api'
var root = 'http://localhost:81/api'
// axios
var axios = require('axios')

function apiAxios(method, url, params, data, success, failure) {
  axios({
    method: method,
    url: url,
    params: params,//urlnull
    data: data,//bodyPUT,POST,PATCHnull
    baseURL: root,
    withCredentials: false//
  })
    .then(function (response) {
      if (success) {//success()
        success(response)//success(),response=>{console.log(response.data)}
      }
    })
    .catch(function (error) {
      if (failure) {//failure()
        failure(error.response)//failure(),error=>{console.log(error.data)}
      }
    })
}

//paramsnull
export default {
  get: function (url, params, data, success, failure) {
    return apiAxios('GET', url, params, data, success, failure)
  },
  post: function (url, params, data, success, failure) {
    return apiAxios('POST', url, params, data, success, failure)
  },
  put: function (url, params, data, success, failure) {
    return apiAxios('PUT', url, params, data, success, failure)
  },
  delete: function (url, params, data, success, failure) {
    return apiAxios('DELETE', url, params, data, success, failure)
  }
}



