</template>

<script>
  export default {
    name: "sideNav",
    data() {
      return {
        style: {
          sideNavUl: {}
        }
      }
    },
    methods: {

      scale(value) {
        return this.$root.scale(value);
      },

      // 
      isSideNavMenuOnHover(isHover) {
        if (isHover == true) {// ''
          this.style.sideNavOnHover = true;
          this.style.sideNavPullDownBox.visibility = 'visible';//
        } else {// ''
          setTimeout(function () {
            if (this.style.sideNavPullDownOnHover == false) {// ms
              this.style.sideNavPullDownBox.visibility = 'hidden'
              this.style.sideNavOnHover = false;
            }
          }.bind(this), 1)
        }
      },

      // 
      isSideNavMenuOptionOnHover(isHover) {
        if (isHover == true) {// 
          this.style.sideNavPullDownOnHover = true;
          if (this.style.sideNavPullDownBox.visibility == 'visible') { // 
            this.style.sideNavDetail.visibility = 'visible';// 
          }
        } else {// 
          setTimeout(function () {
            if (this.style.sideNavPullDownDetailOnHover == false) {// ms
              this.style.sideNavPullDownBox.visibility = 'hidden'
              this.style.sideNavPullDownOnHover = false;
              this.style.sideNavOnHover = false;
              this.style.sideNavDetail.visibility = 'hidden';
            } else {
              this.style.sideNavDetail.visibility = 'visible';
            }
          }.bind(this), 1)
        }
      },

      // 
      isSideNavMenuOptionDetailOnHover(isHover) {
        if (isHover == true) {
          this.style.sideNavPullDownDetailOnHover = true;
        } else {
          this.style.sideNavPullDownDetailOnHover = false;
          this.style.sideNavDetail.visibility = 'hidden';
          this.style.sideNavPullDownOnHover = false;
          setTimeout(function () {
            if (this.style.sideNavPullDownOnHover == false) {// 1ms
              this.style.sideNavPullDownBox.visibility = 'hidden';
            }
          }.bind(this), 1)
        }
      },

      // 
      setSideNavDetail(index) {
        switch (index) {
          case 0:
            this.sideNavDetetail = this.sideNavDetetailBeauty;
            break;
          case 1:
            this.sideNavDetetail = this.sideNavDetetailBaby;
            break;
          case 2:
            this.sideNavDetetail = this.sideNavDetetailBeauty;
            break;
          case 3:
            this.sideNavDetetail = this.sideNavDetetailBeauty;
            break;
          case 4:
            this.sideNavDetetail = this.sideNavDetetailBaby;
            break;
        }
      }
    },
    // methods end
  }
</script>

<style scoped>
  ul {
    list-style-type: none;
  }

  dl, dd, dt, li, ul {
    padding: 0px;
    margin: 0px;
  }


</style>



