<template>
  <div>
    <div :style="style.centerContainer">
      <!---->
      <template v-if="imgUrl">
        <div>
          <img class="avatar" :style="style.avatar.img" :src="imgUrl" @click="chooseFile">
        </div>
      </template>
      <template v-else>
        <div class="avatar" :style="style.avatar.box" @click="chooseFile">
          <div :style="style.avatar.text"></div>
        </div>
      </template>
      <!---->
      <div>
        <el-button :style="style.avatar.uploadButton" type="success"
                   @click="fileUpload" icon="el-icon-upload">
        </el-button>
      </div>
    </div>

    <input type="file" style="display: none" ref="uploadFile" @change="onChoosedFile"/>

  </div>
</template>

<
<script>
  export default {
    name: "avatarUpload",
    data() {
      return {
        style: {
          centerContainer: {
            width: this.scale(300) + 'px',
            display: 'flex',
            justifyContent: 'center',
            flexWrap: 'wrap'
          },
          avatar: {
            box: {
              borderRadius: '50%',
              width: this.scale(250) + 'px',
              height: this.scale(250) + 'px',
              lineHeight: this.scale(250) + 'px',
            },
            text: {
              margin: '0 auto',
              width: '6em',
              fontSize: this.scale(16) + 'px',
              color: '#52eb3d',
            },
            img: {
              width: this.scale(250) + 'px',
              height: this.scale(250) + 'px',// 200
              margin: this.scale(10) + 'px',// 200
              borderRadius: '50%',
            },
            uploadButton: {
              margin: this.scale(10) + 'px',
            }
          }
        },
        disabledUploadButton: true,
        imgUrl: this.$store.state.userInfo.user.userInfo.avatarSrc,
        errorMessage: '',
      };
    },

    methods: {
      scale(value) {
        return this.$root.scale(value);
      },

      chooseFile() {
        if (this.$refs.uploadFile.files != null) {// 
          this.$refs.uploadFile.files = null;
        }
        this.$refs.uploadFile.click()// 
      },
      /**
       * uploadFilefiles
       * @returns {boolean} true;false
       */
      onChoosedFile() {
        let file = this.$refs.uploadFile.files[0];
        if (this.$refs.uploadFile.files != null && file != null) {
          if (file.size / 1024 / 1024 > 2)//2MB
          {
            this.errorMessage = '2MB'
            this.$message({
              message: this.errorMessage,
              type: 'error',
              center: true,//
            });
            return false;
          } else if (file.type != 'image/jpeg' && file.type != 'image/png') {
            //console.log(file.type)
            this.errorMessage = 'jpgpng'
            this.$message({
              message: this.errorMessage,
              type: 'error',
              center: true,//
            });
            return false;
          } else {
            this.errorMessage = null;
            this.imgUrl = URL.createObjectURL((file));//imgUrl,
            return true;
          }
        }
      },
      fileUpload() {
        if (this.errorMessage != null) {
          this.$message({
            message: this.errorMessage,
            type: 'error',
            center: true,//
          });
          return;
        }
        let file = this.$refs.uploadFile.files[0];
        var formData = new FormData();
        formData.append("avatar", file);
        this.$page.put('/user/avatar', null,
          formData,
          response => {
            if (response.data != true) {
              this.$message({
                message: '',
                type: 'error',
                center: true,//
              });
            } else {
              this.$message({
                message: '',
                type: 'success',
                center: true,//
              });
            }
          },
          error => {
            this.$message({
              message: '',
              type: 'error',
              center: true,//
            });
          })
      },
    },
  }
</script>

<style>

  .avatar {
    cursor: pointer;
    border: 2px solid #fff;
  }

  .avatar:hover {
    cursor: pointer;
    border: 2px dashed cyan;
  }


</style>



