<template>

  <dl :style="style.container">
    <!-- title -->
    <dt :style="style.title.box">
      <span :style="style.title.allChecked.box">
        <el-checkbox :style="style.title.allChecked.btn"
                     v-model="allCheckedBtnState"
                     @change="allCheckedBtnChanged">
        </el-checkbox>
      </span>
      <span :style="style.title.goodsInfo">
        <div :style="style.title.textCenterStyle">
                  
        </div>
      </span>
      <span :style="style.title.goodsOriginalPrice">
        <div :style="style.title.textCenterStyle">
                  
        </div>
      </span>
      <span :style="style.title.purchaseQuantity">
        <div :style="style.title.textCenterStyle">
                  
        </div>
      </span>
      <span :style="style.title.totalPrice">
         <div :style="style.title.textCenterStyle">
                  
        </div>
      </span>
      <span :style="style.title.operation">
        <div :style="style.title.textCenterStyle">
                  
        </div>
      </span>
    </dt>
    <!-- content -->
    <dd>
      <ul :style="style.content.box"
          v-for="item in shopcart"
          :key="shopcart.indexOf(item)">
        <li :style="style.content.checkbox">
          <el-checkbox v-model="checkedItems[shopcart.indexOf(item)]"
                       @change="checkBoxChanged"></el-checkbox>
        </li>
        <!--  -->
        <li :style="style.content.goodsImg.li">
          <img :src="item.goodsImg" :style="style.content.goodsImg.img">
        </li>
        <!--  -->
        <li :style="style.title.goodsInfo">
          <!---->
          <div :style="style.content.goodsInfo.text">
            {{item.goodsTitle}}
          </div>
          <!---->
          <div v-for="goodsOption in item.goodsOptions"
               :style="style.content.goodsInfo.goodsOptions">
            {{goodsOption.goodsOptionName}}:&nbsp&nbsp&nbsp&nbsp{{goodsOption.goodsOptionValue}}
          </div>
          <!-- &lt;!&ndash;  &ndash;&gt;
           <div :style="style.content.goodsInfo.label">
             <span :style="style.content.goodsInfo.labelImg" v-for="goodsLabel in item.goodsLabel">
               <img :src="goodsLabel">
             </span>
           </div>-->
        </li>
        <!--  -->
        <li :style="style.title.goodsOriginalPrice">
          <div :style="style.title.textCenterStyle">
            <div v-if="item.goodsOriginalPrice">
              <del :style="style.content.price.originalPrice">{{item.goodsOriginalPrice}}</del>
            </div>
            <div>
              <span :style="style.content.price.discount">{{item.goodsDiscount}}</span>
            </div>
            <div>
            </div>
          </div>
        </li>
        <!--  -->
        <li :style="style.title.purchaseQuantity">
          <div :style="style.title.textCenterStyle">
            <el-input-number size="small"
                             @change="computeGoodsQuantityAndCost"
                             v-model="item.purchaseQuantity"
                             :min="1"
                             :max="999">
            </el-input-number>
          </div>
        </li>
        <!--  -->
        <li :style="style.title.totalPrice">
          <div :style="style.title.textCenterStyle">
            <span :style="style.content.price.totalPrice">{{item.goodsDiscount*item.purchaseQuantity}}</span>
          </div>
        </li>
        <!--  -->
        <li :style="style.title.operation">
          <div :style="style.title.textCenterStyle">
            <div :style="style.content.operation">
              
            </div>
          </div>
          <div :style="style.title.textCenterStyle">
            <div :style="style.content.operation">
              
            </div>
          </div>
        </li>
      </ul>
    </dd>
    <!--  -->
    <dd :style="style.pay.box">
      <div>
        <!--  -->
        <span :style="style.pay.goodsQuantityText">
          <span :style="style.pay.goodsQuantity">{{goodsQuantity}}</span>
        </span>
        <!--  -->
        <span :style="style.pay.costText">
          :<span :style="style.pay.cost">{{cost}}</span>
        </span>
        <!--  -->
        <span :style="style.pay.payMoney.box">
          <el-button id="payMoney"
                     :style="style.pay.payMoney.btn"
                     @click="payMoneyClick"></el-button>
        </span>
      </div>
    </dd>
  </dl>

</template>

<script>
  export default {
    name: "shopcart",
    data() {
      return {
        style: {
          container: {
            marginBottom: this.scale(100) + 'px',
            fontSize: this.scale(0) + 'px',
          },
          title: {
            box: {
              border: '1px solid #b5b8b2',
              paddingLeft: this.scale(15) + 'px',
              height: this.scale(40) + 'px',
              lineHeight: this.scale(40) + 'px',
              color: '#999',
            },
            textCenterStyle: {
              margin: '0 auto',
              width: 'max-content',
            },
            allChecked: {
              box: {
                fontSize: this.scale(13) + 'px',
                display: 'inline-block',
                width: this.scale(125) + 'px',
                verticalAlign: 'middle',
              },
              btn: {
                color: '#000',
                fontWeight: 'bold',
              }
            },
            goodsInfo: {
              fontSize: this.scale(13) + 'px',
              display: 'inline-block',
              marginLeft: this.scale(20) + 'px',
              width: this.scale(160) + 'px',
              verticalAlign: 'middle',
            },
            goodsOriginalPrice: {
              fontSize: this.scale(13) + 'px',
              display: 'inline-block',
              marginLeft: this.scale(20) + 'px',
              width: this.scale(160) + 'px',
              verticalAlign: 'middle',
            },
            purchaseQuantity: {
              fontSize: this.scale(13) + 'px',
              display: 'inline-block',
              marginLeft: this.scale(20) + 'px',
              width: this.scale(150) + 'px',
              verticalAlign: 'middle',
            },
            totalPrice: {
              marginLeft: this.scale(20) + 'px',
              fontSize: this.scale(13) + 'px',
              display: 'inline-block',
              width: this.scale(160) + 'px',
              verticalAlign: 'middle',
            },
            operation: {
              fontSize: this.scale(13) + 'px',
              display: 'inline-block',
              marginLeft: this.scale(20) + 'px',
              width: this.scale(150) + 'px',
              verticalAlign: 'middle',
            }
          },
          content: {
            box: {
              borderLeft: '1px solid #b5b8b2',
              borderRight: '1px solid #b5b8b2',
              borderBottom: '1px solid #b5b8b2',
              display: 'inline-block',
              padding: this.scale(20) + 'px',
              width: this.scale(1050 - 40 - 2) + 'px',
            },
            checkbox: {
              display: 'inline-block',
              width: this.scale(40) + 'px',
              verticalAlign: 'middle',
            },
            goodsImg: {
              li: {
                width: this.scale(80) + 'px',
                height: this.scale(80) + 'px',
                display: 'inline-block',
                verticalAlign: 'middle',
              },
              img: {
                width: this.scale(80) + 'px',
                height: this.scale(80) + 'px',
              }
            },
            goodsInfo: {
              text: {
                fontSize: this.scale(13) + 'px',
              },
              goodsOptions: {
                marginTop: this.scale(5) + 'px',
                fontSize: this.scale(12) + 'px',
              },
              label: {
                marginTop: this.scale(10) + 'px'
              },
              labelImg: {
                marginRight: this.scale(7) + 'px',
              }
            },
            price: {
              originalPrice: {
                color: '#9c9c9c',
                fontSize: this.scale(13) + 'px',
              },
              discount: {
                color: '#000',
                fontSize: this.scale(14) + 'px',
                fontWeight: 'bold',
              },
              totalPrice: {
                color: '#f40',
                fontSize: this.scale(14) + 'px',
                fontWeight: 'bold',
              }
            },
            operation: {
              fontSize: this.scale(12) + 'px',
              marginBottom: this.scale(15) + 'px',
              cursor: 'pointer',
            }
          },
          pay: {
            box: {
              width: this.scale(1050) + 'px',
              height: this.scale(50) + 'px',
              lineHeight: this.scale(50) + 'px',
              position: 'fixed',
              bottom: this.scale(-1) + 'px',
              zIndex: 9999,
              background: '#c9c5c5',
            },
            goodsQuantityText: {
              display: 'inline-block',
              fontSize: this.scale(14) + 'px',
              marginLeft: this.scale(470) + 'px',
              width: this.scale(178) + 'px',
            },
            goodsQuantity: {
              marginLeft: this.scale(5) + 'px',
              marginRight: this.scale(5) + 'px',
              color: '#f40',
              fontSize: this.scale(17) + 'px',
              fontWeight: 'bold',
            },
            costText: {
              display: 'inline-block',
              marginLeft: this.scale(50) + 'px',
              fontSize: this.scale(14) + 'px',
              width: this.scale(212) + 'px',
            },
            cost: {
              marginLeft: this.scale(7) + 'px',
              marginRight: this.scale(5) + 'px',
              color: '#f40',
              fontSize: this.scale(19) + 'px',
              fontWeight: 'bold',
            },
            payMoney: {
              box: {
                marginLeft: this.scale(10) + 'px',
              },
              btn: {
                fontSize: this.scale(15) + 'px',
              }
            }
          },
        },// style end
        allCheckedBtnState: false,// ,true
        checkedItems: [],// 
        shopcart:
          [
            {
              goodsImg: 'https://img.alicdn.com/bao/uploaded/i3/647714826/TB2AV2NGHSYBuNjSspiXXXNzpXa_!!647714826.jpg_80x80.jpg',
              goodsTitle: '',
              goodsOptions: [// ordergoods_option
                {
                  goodsOptionName: '',
                  goodsOptionValue: 'S',
                },
                {
                  goodsOptionName: '',
                  goodsOptionValue: '',
                },
                {
                  goodsOptionName: '',
                  goodsOptionValue: '32GB',
                },
              ],
              goodsLabel: [
                'https://img.alicdn.com/tps/i3/TB1dtEWFVXXXXXwaXXXAz6UFXXX-16-16.png',
                'https://assets.alicdn.com/sys/common/icon/trade/xcard.png',
                'https://img.alicdn.com/tps/TB1GnsWJXXXXXbCXpXXXXXXXXXX-43-16.png'
              ],
              goodsOriginalPrice: 38,
              goodsDiscount: 15,
              purchaseQuantity: 1,// 
            },
            {
              goodsImg: 'https://img.alicdn.com/bao/uploaded/i3/TB1nri1piMnBKNjSZFzxuI_qVXa_031333.jpg_80x80.jpg',
              goodsTitle: '3//2Apple/ iPhone X x ',
              goodsOptions: [
                {
                  goodsOptionName: '',
                  goodsOptionValue: 'M',
                },
                {
                  goodsOptionName: '',
                  goodsOptionValue: '',
                },
                {
                  goodsOptionName: '',
                  goodsOptionValue: '32GB',
                },
              ],
              goodsLabel: [
                'https://assets.alicdn.com/sys/common/icon/trade/xcard.png',
                'https://gtd.alicdn.com/tps/i7/T1g9BqXoJfXXXXXXXX-16-16.png',
              ],
              goodsOriginalPrice: null,
              goodsDiscount: 6268.00,
              purchaseQuantity: 1,// 
            },
            {
              goodsImg: 'https://img.alicdn.com/bao/uploaded/i4/20454572/O1CN011je1HTrNnNg5lIE_!!20454572.jpg_80x80.jpg',
              goodsTitle: 'MSI/ GeForce RTX 2070 ARMOR 8G  ',
              goodsOptions: [
                {
                  goodsOptionName: '',
                  goodsOptionValue: 'M',
                },
                {
                  goodsOptionName: '',
                  goodsOptionValue: '',
                },
                {
                  goodsOptionName: '',
                  goodsOptionValue: '32GB',
                },
              ],
              goodsLabel: [
                'https://img.alicdn.com/tps/i3/TB1dtEWFVXXXXXwaXXXAz6UFXXX-16-16.png',
                'https://assets.alicdn.com/sys/common/icon/trade/xcard.png',
                'https://img.alicdn.com/tps/i3/T1bnR4XEBhXXcQVo..-14-16.png'
              ],
              goodsOriginalPrice: null,
              goodsDiscount: 3999.00,
              purchaseQuantity: 1,// 
            },
            {
              goodsImg: 'https://img.alicdn.com/bao/uploaded/i4/20454572/O1CN01LGQxxU1je1IAiFma6_!!20454572.jpg_80x80.jpg',
              goodsTitle: 'Intel/ i5-8600K i5-8600K 3.6G 668CPU',
              goodsOptions: [
                {
                  goodsOptionName: '',
                  goodsOptionValue: 'L',
                },
                {
                  goodsOptionName: '',
                  goodsOptionValue: '',
                },
                {
                  goodsOptionName: '',
                  goodsOptionValue: '64GB',
                },
              ],
              goodsLabel: [
                'https://img.alicdn.com/tps/i1/T1EQA5FpVgXXceOP_X-16-16.jpg',
                'https://img.alicdn.com/tps/TB1GnsWJXXXXXbCXpXXXXXXXXXX-43-16.png',
              ],
              goodsOriginalPrice: 1988.00,
              goodsDiscount: 1888.00,
              purchaseQuantity: 1,// 
            },
          ],
        goodsQuantity: 0,// 
        cost: 0,// 

      };
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      /**
       * 
       */
      checkBoxChanged() {
        if (this.checkedItems.every((value, index, array) => value == true)) {
          this.allCheckedBtnState = true
        } else {
          this.allCheckedBtnState = false
        }
        this.computeGoodsQuantityAndCost();
      },
      /**
       * 
       */
      allCheckedBtnChanged() {
        if (this.allCheckedBtnState == true) {
          this.checkedItems.forEach((value, index, array) => {
            array[index] = true;
          })
        } else {
          this.checkedItems.forEach((value, index, array) => {
            array[index] = false;
          })
        }
        this.computeGoodsQuantityAndCost();
      },
      /**
       * 
       */
      computeGoodsQuantityAndCost() {
        this.goodsQuantity = 0;
        this.cost = 0;
        this.checkedItems.forEach(((value, index) => {
          if (value == true) {
            this.goodsQuantity += this.shopcart[index].purchaseQuantity
            this.cost += this.shopcart[index].goodsDiscount * this.shopcart[index].purchaseQuantity;
          }
        }))
      },
      payMoneyClick() {
        if (this.goodsQuantity <= 0)
          return;
      }
    },
    beforeMount() {
      this.shopcart.forEach((value, index) => {
        this.checkedItems[index] = false;// 
      })
    },
    /* mounted() {
       this.$store.dispatch('user/updateBodyHeight', document.documentElement.scrollHeight + 'px')
     }*/
  }
</script>

<style scoped>

  span, div, dl, dd, dt, li, ul {
    padding: 0px;
    margin: 0px;
    border: 0px;
    list-style-type: none;
  }

  .aaa {
    vertical-align: middle;
  }

  #payMoney {
    background: #f87735;
    color: #000;
  }

  #payMoney:hover {
    background: #e70d1d;
    color: #fff;
  }
</style>



