// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router/index'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import store from './store/index.js'
import promise from 'es6-promise' //iePromise
import $ from 'jquery/dist/jquery.min.js'
import api from './api/api.js'
import page from './api/page.js'
import authorization from './api/authorization.js'
import 'babel-polyfill'

Vue.config.productionTip = false// vue 
Vue.use(ElementUI);
Vue.prototype.$api = api;//apithis.$api.get()/post()/delete()/put()
Vue.prototype.$page = page;//this.$page.get()/post()/delete()/put()
Vue.prototype.$authorization = authorization;//this.$authorization.get()/post()/delete()/put()
promise.polyfill();////iePromise

//
router.beforeEach((to, from, next) => {
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  // IE11 
  if (
    '-ms-scroll-limit' in document.documentElement.style &&
    '-ms-ime-align' in document.documentElement.style
  ) { // detect it's IE11
    window.addEventListener("hashchange", function (event) {
      let currentPath = window.location.hash.slice(1);
      if (to !== currentPath) {
        router.push(currentPath)
      }
    }, false)
  }
  next();
})

/* eslint-disable no-new */
var vm = new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  template: '<App/>',
  data() {
    return {
      beforeAuthorizationPath: null,//
    }
  },
  methods: {
    /**
     *  : value
     *  :
     *  1. <meta name="viewport" content="width=device-width,initial-scale=0.1">
     *  2. :this.scale(1366)+'px',
     *  3. spansm
     *  4. scale(value){return this.$root.scale(value);}scale()
     *  5. %.
     * @param value 
     * @returns {number}
     */
    scale(value) {
      //parseIntelspan,push
      return parseInt(value * window.screen.width * window.devicePixelRatio / 1366.0)
    },
    /**
     * 
     * created():this.$root.handleLoginAuthorization("/");
     *
     * @param redirectPath 
     */
    handleLoginAuthorization(redirectPath) {
      this.beforeAuthorizationPath = this.$route.path;//
      let _this = this;
      $.ajax({
        type: "get",
        url: '/authorization/login',
        //url: '/authorization' + _this.$route.path,
        async: false,//
        success: function (data) {
          _this.$store.dispatch('userInfo/updateUser', data)
        },
        error: function (data) {//,4**5**
          _this.$router.push(redirectPath)
          /* if (data.status == '401') {
             _this.$router.push(redirectPath)
           } else {
             _this.$router.push('/' + data.status)//
           }*/
        }
      })
    },
  },
});





