<template>
  <div>
    <el-form ref="registeForm" status-icon :model="registeForm" :rules="rules">
      <!---->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.email">
          <el-form-item prop="email">
            <el-input type="text" v-model="registeForm.email" autocomplete="on" placeholder="">
              <i slot="prefix" class="email"></i>
            </el-input>
          </el-form-item>
        </div>
      </div>
      <!---->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.username">
          <el-form-item prop="username">
            <el-input type="text" v-model="registeForm.username" autocomplete="off" placeholder="">
              <i slot="prefix" class="username"></i>
            </el-input>
          </el-form-item>
        </div>
      </div>
      <!---->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.password">
          <el-form-item prop="password">
            <el-input type="password" v-model="registeForm.password" autocomplete="off" placeholder="">
              <i slot="prefix" class="password"></i>
            </el-input>
          </el-form-item>
        </div>
      </div>
      <!---->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.password">
          <el-form-item prop="confirmPassword">
            <el-input type="password" v-model="registeForm.confirmPassword" autocomplete="off" placeholder="">
              <i slot="prefix" class="password"></i>
            </el-input>
          </el-form-item>
        </div>
      </div>
      <!---->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.captcha.input">
          <el-form-item prop="captcha">
            <el-input type="text" v-model="registeForm.captcha" autocomplete="off" maxLength=4
                      placeholder="">
              <i slot="prefix" class="captcha"></i>
            </el-input>
          </el-form-item>
        </div>
        <img @click="getCaptcha"
             :style="style.registeForm.captcha.img"
             :src="captchaImgSrc" alt=""/>
      </div>
      <!---->
      <div :style="style.registeForm.itemBox">
        <div :style="style.registeForm.registeBtn">
          <el-button type="primary" @click="onSubmit"></el-button>
        </div>
      </div>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: 'registe',
    data() {
      let _this = this;
      var validateEmail = (rule, value, callback) => {
        _this.$page.post(_this.baseContextPath + '/checkEmail', null, {'email': _this.registeForm.email},
          response => {
            if (response.data !== true) {
              callback(new Error(','));
            } else {
              callback();
            }
          });
      };
      var validateUsername = (rule, value, callback) => {
        if (/^[0-9]/.test(value)) {
          callback(new Error(''))
        } else if (/[^a-zA-Z0-9_]/.test(value)) {
          callback(new Error(','))
        }
        else {
          _this.$page.post(_this.baseContextPath + '/checkUserName', null, {'username': _this.registeForm.username},
            response => {
              if (response.data !== true) {
                callback(new Error(','))
              } else {
                callback();
              }
            });
        }
      };
      var validatePassword = (rule, value, callback) => {

        if (/^[0-9]*$/.test(value)) {
          callback(new Error(''))
        } else if (/^[a-zA-Z]*$/.test(value)) {
          callback(new Error(''))
        } else {
          if (_this.registeForm.confirmPassword !== '') {//
            _this.$refs.registeForm.validateField('confirmPassword');
          }
          callback();
        }

      };
      var validateConfirmPassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== _this.registeForm.password) {//
          callback(new Error('!'));//: 
        } else {
          callback();
        }
      };
      var validateCaptcha = (rule, value, callback) => {
        _this.$page.post(_this.baseContextPath + '/checkCaptcha', null, {'captcha': _this.registeForm.captcha},
          response => {
            if (response.data !== true) {
              callback(new Error(''));
            }
            else {
              _this.isCaptchaValidate = true;//
              _this.style.cursor = null;
              callback();
            }
          });
      };
      return {
        style: {
          registeForm: {
            itemBox: {
              width: this.scale(350) + 'px',
              margin: this.scale(20) + "px " + 'auto',
            },
            email: {
              verticalAlign: "middle",
            },
            username: {
              verticalAlign: "middle",
            },
            password: {
              verticalAlign: "middle",
            },
            captcha: {
              input: {
                display: 'inline-block',
                width: this.scale(200) + 'px',
                verticalAlign: "middle",
              },
              img: {
                display: 'inline-block',
                fontSize: this.scale(10) + 'px',
                verticalAlign: "middle",
                cursor: 'pointer',
                width: this.scale(100) + 'px',
                marginLeft: this.scale(10) + 'px'
              },
            },
            registeBtn: {
              margin: '0 auto',
              width: this.scale(80) + 'px'
            }
          }
        },
        registeForm: {
          email: null,
          username: null,
          password: null,
          confirmPassword: null,
          captcha: null
        },
        rules: {
          email: [{type: "email", message: '', required: true, trigger: ['blur']},
            {validator: validateEmail, trigger: ['blur']}],
          username: [{required: true, message: '', trigger: ['blur']},
            {validator: validateUsername, trigger: ['change', 'blur']}],
          password: [{required: true, message: '', trigger: ['blur']},
            {min: 6, message: '6', trigger: ['blur']},
            {validator: validatePassword, trigger: ['change', 'blur']}],
          confirmPassword: [{required: true, message: '', trigger: ['blur']},
            {min: 6, message: '6', trigger: ['blur']},
            {validator: validateConfirmPassword, trigger: ['change', 'blur']}],
          captcha: [
            {required: true, message: '', trigger: ['blur']},
            {validator: validateCaptcha, trigger: ['blur']}
          ],
        },
        baseContextPath: this.$route.path + '/' + this.$options.name,
        captchaImgSrc: null,//nginx
        isCaptchaValidate: false,
      }
    },
    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      onSubmit() {
        let _this = this;//let,: vueThis
        this.$refs.registeForm.validate((valid) => {//().
          if (valid) {//
            _this.$page.post(_this.baseContextPath + '/handleRegiste', null, _this.registeForm,
              response => {
                _this.$alert(',!', '', {
                  confirmButtonText: '',
                  callback: (action, instance) => {
                    setTimeout(function () {
                      window.location.reload()// login
                    }, 200)
                  }
                });
              },
              error => {
                _this.$message({
                  message: "!",
                  type: 'error',
                  center: true,
                });
              });
          } else {
            return false;//false
          }
        });
      },
      getCaptcha() {
        let _this = this;
        if (!this.isCaptchaValidate) {//
          _this.$page.get(_this.baseContextPath + '/getCaptcha', null, null,
            response => {
              _this.registeForm.captcha = null
              _this.captchaImgSrc = '/temp/images/captcha/' + response.data;
            }
          );
        }
      },
    },
    mounted() {
    }
  }
</script>

<style scoped>

  .el-form-item {
    margin-bottom: 0px;
  }

  .email {
    background: url(../../assets/user/email-icon.png) center no-repeat;
    background-size: cover;
  }

  .email:before {
    content: "";
    font-size: 16px;
    visibility: hidden;
  }

  .username {
    background: url(../../assets/user/username-icon.png) center no-repeat;
    background-size: cover;
  }

  .username:before {
    content: "";
    font-size: 16px;
    visibility: hidden;
  }

  .captcha {
    background: url(../../assets/user/captcha-icon.png) center no-repeat;
    background-size: cover;
  }

  .captcha:before {
    content: "";
    font-size: 16px;
    visibility: hidden;
  }

  .password {
    background: url(../../assets/user/password-icon.png) center no-repeat;
    background-size: cover;
  }

  .password:before {
    content: "";
    font-size: 16px;
    visibility: hidden;
  }

</style>



