        goodsDetailCategoryNames: [],
        goodsDetailCategoryValues: [],// goods
        goodsParent: [],
        pagiGoodsParent: [],// goodsParent;
        maxRowCategoryValueCount: this.maxRowCategoryValueCount,// 
        divide: [],// 
        goodsL2CategoryDic: [],// dividecategory_id
        showDivideMore: false,// ,
        showDivideMore: false,// ,
        //pageSize: 2,// 
