    },

    methods: {
      scale(value) {
        return this.$root.scale(value);
      },

      /**
       * 
       *
       */
      selectAllGoodsParent() {
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues",
          [
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
          ]
        );
        this.$store.commit("goodsShow/updateNavValue", "")
        this.$store.commit("goodsShow/updateCurrentCategoryId", null);
        //this.$store.commit("goodsShow/updateGoodsCategoryValue", "");
        this.$store.commit("goodsShow/updateSearchValue", "");
        //this.$store.commit("goodsShow/updateMinPrice", 0);
        //this.$store.commit("goodsShow/updateMaxPrice", 999999999);
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        //this.getGoodsParentAndUserSelectedGoodsDetailCategory();
        document.location.hash = "/";// query
        this.$router.push({name: 'goodsDetailCategory'})
      },

      clickNavi() {
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues",
          [
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
          ]
        );
        this.getGoodsParentAndUserSelectedGoodsDetailCategory();
      },

      deleteSelectedCategory(userSelectedGoodsDetailCategoryIndex) {
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        let userSelectedGoodsDetailCategoryValues = JSON.parse(JSON.stringify(this.$store.state.goodsShow.userSelectedGoodsDetailCategoryValues));
        userSelectedGoodsDetailCategoryValues[userSelectedGoodsDetailCategoryIndex].text = null;
        userSelectedGoodsDetailCategoryValues[userSelectedGoodsDetailCategoryIndex].id = null;
        userSelectedGoodsDetailCategoryValues[userSelectedGoodsDetailCategoryIndex].categoryValueOrder = null;
        userSelectedGoodsDetailCategoryValues[userSelectedGoodsDetailCategoryIndex].fieldIndex = null;
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues", userSelectedGoodsDetailCategoryValues);
        this.getGoodsParentAndUserSelectedGoodsDetailCategory()

      },

      /**
       * 
       */
      goodsDetailCategoriesValueClick(rowIndex, goodsDetailCategoriesValue) {
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        let userSelectedGoodsDetailCategoryValues = JSON.parse(JSON.stringify(this.$store.state.goodsShow.userSelectedGoodsDetailCategoryValues));
        userSelectedGoodsDetailCategoryValues[rowIndex] = goodsDetailCategoriesValue;
        this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategoryValues", userSelectedGoodsDetailCategoryValues);
        this.getGoodsParentAndUserSelectedGoodsDetailCategory();
      },

      /**
       * 
       */
      divideItemClick(event, categoryId) {
