            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
            {text: null, id: null, categoryValueOrder: null, fieldIndex: null},
