          ]
        );
        this.$store.commit("goodsShow/updateCurrentPage", 1);
        this.$store.commit("goodsShow/updateCurrentCategoryId", categoryId);
        this.getGoodsCategoryValue();
        /* if (this.$store.state.goodsShow.currentCategoryId != null) {
           this.$store.commit("goodsShow/updateNavValue", this.$store.state.goodsShow.goodsCategoryValue)
         } else {
           this.$store.commit("goodsShow/updateNavValue", this.$store.state.goodsShow.searchValue)
         }*/
        this.getGoodsParentAndUserSelectedGoodsDetailCategory();
        let goodsDetailCategoriesValue = document.querySelectorAll(".goodsDetailCategoriesValue");
        goodsDetailCategoriesValue.forEach(value => {
          value.style.color = "#000";
        })
        event.currentTarget.style.color = "red"
      },

      /**
       * goodsDetailCategorycurrentCategoryIdgoodsCategoryValue
       */
      getGoodsCategoryValue() {
        if (this.$store.state.goodsShow.currentCategoryId != null) {
          this.$page.get('goods/goodsCategoryValue', {id: this.$store.state.goodsShow.currentCategoryId}, null,
            response => {
              //this.$store.commit("goodsShow/updateGoodsCategoryValue", response.data.goodsCategoryValue);
              this.$store.commit("goodsShow/updateNavValue", response.data.goodsCategoryValue)
              return;
            },
            /* error => {
               this.userSelectedGoodsDetailCategories = null;
             }*/
          )
        }
        this.$store.commit("goodsShow/updateNavValue", this.$store.state.goodsShow.searchValue)
      },

      /*getGoodsParent() {
        let params = {
          categoryId: this.currentCategoryId,
          searchValue: this.searchValue,
          orderBy: 'category_id',
          pageNum: 0,
          pageCount: 1,
        };
        this.$page.get('goods/goodsParent', params,
          response => {
            this.goodsParent = response.data;
          },
          error => {
            this.goodsParent = null;
            this.userSelectedGoodsDetailCategories = null;
          })
      },*/

      /**
       * 
       */
      generateDivide() {
        this.divide = [];// 
        this.goodsParent.forEach((value, index) => {
          if (this.divide.every(categoryId => categoryId != value.categoryId)) {// dividevalue.categoryId
            this.divide.push(value.categoryId);// 
          }
        })
        //this.divide.sort((a, b) => a - b);
        //if (this.divide.length > 1) {
        if (this.divide.length > 1) {
          if (this.goodsL2CategoryDic.length == 0) {// 
            this.$page.get("/goods/goodsL2Category", null, null,
              response => {
                this.goodsL2CategoryDic = response.data;
                this.divide.sort((a, b) => a - b)
              })
          }
          let goodsDetailCategoriesValue = document.querySelectorAll(".goodsDetailCategoriesValue");
          goodsDetailCategoriesValue.forEach(value => {
            value.style.color = "#000";
          })
        }else {
          this.divide = [];// 
        }
      },

      handleCategoryIsNotNull(response) {
        // 
        this.getGoodsDetailCategoryName()
      },

      /**
       * 
       * 
       */
      handleCategoryIsNull(response) {
        //this.$store.commit("goodsShow/updateCurrentCategoryId", this.goodsParent[0].categoryId)
        //this.currentCategoryId = this.goodsParent[0].categoryId;
        // 
        this.getGoodsDetailCategoryName()
      },

      /**
       * goodsParent
       *
       */
      getGoodsParentAndUserSelectedGoodsDetailCategory() {
        let params = {
          categoryId: this.$store.state.goodsShow.currentCategoryId,
          searchValue: this.$store.state.goodsShow.searchValue,
          orderBy: this.$store.state.goodsShow.orderBy,
          currentPage: this.$store.state.goodsShow.currentPage,// 
          pageSize: this.$store.state.goodsShow.pageSize,
          minPrice: this.$store.state.goodsShow.minPrice,
          maxPrice: this.$store.state.goodsShow.maxPrice,
        };
        let bodyData = this.$store.state.goodsShow.userSelectedGoodsDetailCategoryValues;
        /*let bodyData = JSON.parse(JSON.stringify(this.$store.state.goodsShow.userSelectedGoodsDetailCategoryValues));
        bodyData.forEach((v, i, array) => {
          delete array[i].text;
          delete array[i].categoryValueOrder;
        })*/
        this.$page.post('goods/goodsParent', params, bodyData,
          response => {

            this.goodsParent = response.data.goods_parent_list_key;
            this.$store.commit("goodsShow/updateItemCount", response.data.item_count_key)// 
            let startIndex = (this.$store.state.goodsShow.currentPage - 1) * this.$store.state.goodsShow.pageSize;
            let endIndex = this.$store.state.goodsShow.currentPage * this.$store.state.goodsShow.pageSize;
            this.pagiGoodsParent = this.goodsParent.filter((value, index) => index >= startIndex && index < endIndex)
            this.generateDivide();
            this.getGoodsDetailCategoryName()

            //this.handleCategoryIsNull(response);

           /* if (this.$store.state.goodsShow.currentCategoryId == null) {
              this.handleCategoryIsNull(response);
            } else {
              this.handleCategoryIsNotNull(response);
            }*/
          },
          error => {
            if (error.status === 404) {
              this.$message({
                message: '!',
                type: 'warning',
                center: true,//
              });
            } else if (error.status >= 500) {
              this.$message({
                message: ',!',
                type: 'warning',
                center: true,//
              });
            }
            this.$store.commit("goodsShow/updateItemCount", 0)
            this.pagiGoodsParent = [];
            this.goodsParent = [];
            this.goodsDetailCategoryValues = [];
            this.divide = [];
          })
      },

      /**
       * goodsParentdetailCategoryValue
       */
      generateGoodsDetailCategoriesValues() {
        this.goodsDetailCategoryValues = [];
        for (let i = 0; i < 6; i++) {
          this.goodsDetailCategoryValues.push([]);
          this.goodsParent.forEach((goodsParentItem, index, array) => {
            this.goodsDetailCategoryValues[i].push({text: null, id: null, categoryValueOrder: null, fieldIndex: null})//textidgoodsDetailCategoryValueid.order
            // 
            if (goodsParentItem.categoryId == this.goodsParent[0].categoryId) {
              if (goodsParentItem['detailCategoryValue' + i] != null) {// detailCategoryValue
                // 
                if (this.goodsDetailCategoryValues[i].every(goodsDetailCategoriesValue => goodsDetailCategoriesValue.id != goodsParentItem['detailCategoryValue' + i])) {
                  let goodsDetailCategoryValue = this.goodsDetailCategoryValuesDic.find(value => value.id == goodsParentItem['detailCategoryValue' + i]);
                  let goodsDetailCategoryName = this.goodsDetailCategoryNames.find(value => value.id == goodsDetailCategoryValue.goodsDetailCategoryId);
                  let detailCategoryNameOrder = goodsDetailCategoryName.detailCategoryOrder;
                  this.goodsDetailCategoryValues[detailCategoryNameOrder][index].id = goodsParentItem['detailCategoryValue' + i];
                  this.goodsDetailCategoryValues[detailCategoryNameOrder][index].fieldIndex = i;// detail_category_value
                  this.goodsDetailCategoryValues[detailCategoryNameOrder][index].text = goodsDetailCategoryValue.categoryValue;//text
                  this.goodsDetailCategoryValues[detailCategoryNameOrder][index].categoryValueOrder = goodsDetailCategoryValue.categoryValueOrder;
                }
              }
            }
          })
          // goodsParentItem.categoryId == this.goodsParent[0].categoryIddetailCategoryValue
          this.goodsDetailCategoryValues[i] = this.goodsDetailCategoryValues[i].filter((value, index, array) => {
              return value.text != '' && value.text != null
            }
          )
          // categoryValueOrder
          this.goodsDetailCategoryValues[i].sort((a, b) => a.categoryValueOrder - b.categoryValueOrder)
        }

      },

      /**
       * goods_detail_category_name
       */
      getGoodsDetailCategoryName() {

        // 
        let categoryId = this.goodsParent[0].categoryId;
        this.$page.get('goods/goodsDetailCategoryNames', {categoryId: categoryId}, null,
          response => {
            this.goodsDetailCategoryNames = response.data.goods_detail_category_name_list_key;
            //this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategories", response.data.user_selected_goods_detail_category_list_key)
            //this.userSelectedGoodsDetailCategories = response.data.user_selected_goods_detail_category_list_key;
            this.goodsDetailCategoryValuesDic = response.data.goods_detail_category_value_list_key;
            this.goodsDetailCategoryNames.forEach((value, index) => {
              this.$set(value, 'show', false)// $set()
            })
            //console.log(this.userSelectedGoodsDetailCategories)
            this.generateGoodsDetailCategoriesValues();
          },
          /* error => {
             this.goodsParent = null;
             this.userSelectedGoodsDetailCategories = null;
           }*/
        )
      },

      replaceGoodsDetailCategoryValue(goodsDetailCategoriesValue) {
        for (let i = 0; i < this.goodsDetailCategoryValuesDic.length; i++) {
          if (this.goodsDetailCategoryValuesDic[i].id == goodsDetailCategoriesValue) {
            return this.goodsDetailCategoryValuesDic[i].categoryValue;
          }
        }
      },

      replaceCategoryIdToValue(categoryId) {
        for (let i = 0; i < this.goodsL2CategoryDic.length; i++) {
          if (this.goodsL2CategoryDic[i].id == categoryId) {
            return this.goodsL2CategoryDic[i].value;
          }
        }
      },

      showMoreCategoryClick(rowIndex) {
        /* let userSelectedGoodsDetailCategories = JSON.parse(JSON.stringify(this.$store.state.goodsShow.userSelectedGoodsDetailCategories));
         userSelectedGoodsDetailCategories[rowIndex].show = !userSelectedGoodsDetailCategories[rowIndex].show;
         this.$store.commit("goodsShow/updateUserSelectedGoodsDetailCategories", userSelectedGoodsDetailCategories)*/
        this.goodsDetailCategoryNames[rowIndex].show = !this.goodsDetailCategoryNames[rowIndex].show;
      },

      setGoodsDetailCategoryNameStyle() {
        // 
        let divideName = document.querySelector("#divideName");
        if (divideName != null) {
          let divideValues = document.querySelector("#divideValues");
          divideName.style.height = divideValues.offsetHeight - this.scale(10) + 'px';// paddingTop
        }

        // goodsDetailCategoriesValueBoxgoodsDetailCategoryName
        if (this.goodsDetailCategoryNames != null && this.goodsDetailCategoryNames.length > 0) {
          this.goodsDetailCategoryNames.forEach((value, index) => {
            let goodsDetailCategoriesValueBox = document.querySelector("#goodsDetailCategoriesValueBox" + index);
            if (goodsDetailCategoriesValueBox != null || goodsDetailCategoriesValueBox != undefined) {
              let goodsDetailCategoryName = document.querySelector("#goodsDetailCategoryName" + index);
              goodsDetailCategoryName.style.height = goodsDetailCategoriesValueBox.offsetHeight - this.scale(10) + 'px';// paddingTop
            }
          })
        }
      }
    },
    beforeCreate() {
      this.maxRowCategoryValueCount = 5;
    },

    mounted() {
      //this.getGoodsParentAndUserSelectedGoodsDetailCategory(this.$route.query);
    },

    updated() {
      this.setGoodsDetailCategoryNameStyle();
    },

    beforeRouteEnter(to, from, next) {
      next(vm => {
        if (to.query.categoryId != null) {// categoryIdcategoryId
          vm.$store.commit("goodsShow/updateCurrentCategoryId", to.query.categoryId);

        }
        if (to.query.searchValue != null) {// searchValuesearchValue
          vm.$store.commit("goodsShow/updateSearchValue", to.query.searchValue);
        }
        //vm.currentCategoryId = to.query.categoryId;
        //vm.searchValue = to.query.searchValue;
        vm.getGoodsCategoryValue();
        //vm.getGoodsParentAndUserSelectedGoodsDetailCategory(vm.currentCategoryId, vm.searchValue);
        vm.getGoodsParentAndUserSelectedGoodsDetailCategory();
      })
    },
  }
</script>

<style scoped>
  dl, dt, dd, ul, ol, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .clear_space {
    font-size: 0;
  }

  .text_hover:hover {
    cursor: pointer;
    color: #ff4d51;
    text-decoration: underline;
  }

  #category_box:hover :nth-child(3) {
    background-color: #e31436;
    color: white;
  }

  .goodsDetailCategoriesValue:hover {
    color: #e31436;
    cursor: pointer;
  }
</style>



