function scale(value) {
  //parseIntelspan,push
  return parseInt(value * window.screen.width * window.devicePixelRatio / 1366.0)
}

const state = {
  user: {
    id: null,
    username: null,
    userInfo: {
      nickname: null,
      sex: null,
      birthday: 0,
      hometown: "['510000', '510100', '510116']",
      deliverAddress: "['510000', '510100', '510116']",// 
      addressDetail: null,// 
      avatarSrc: '',
    }
  }, // 
}

const getters = {
  /*height() {
    return state.userInfo.style.container.height;
  }*/
}

const mutations = {
  updateUser(state, value) {
    state.user = value;
  },
}

const actions = {
  updateUser({commit}, value) {
    commit('updateUser', value);
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



