    methods: {
      scale(value) {
        return this.$root.scale(value);
      },
      /**
       * 
       * @param event
       **/
      getEventElementStyle(event) {
        let style = event.currentTarget.style;
        return style;
      },
      /**
       *  
       * @param goodsOptionValueBoxId id
       * @param goodsOptionValueSelectedBoxId id
       * @param clickedOptionRowIndex 
       * @param clickedOptionColIndex 
       */
      addClickedStyle(goodsOptionValueBoxId, goodsOptionValueSelectedBoxId, clickedOptionRowIndex, clickedOptionColIndex) {
        let goodsOptionValueBox = document.querySelector('#' + goodsOptionValueBoxId + clickedOptionRowIndex + clickedOptionColIndex);
        //
        let goodsOptionValueSelectedBox = document.querySelector('#' + goodsOptionValueSelectedBoxId + clickedOptionRowIndex + clickedOptionColIndex);
        goodsOptionValueBox.style.color = '#d41c44';
        goodsOptionValueSelectedBox.style.visibility = 'visible';
        if (this.isUserSelectedGoodsOptionStockNumberEnough()) {
          goodsOptionValueBox.style.margin = this.scale(0) + 'px';
          goodsOptionValueBox.style.border = this.scale(2) + 'px solid #d41c44'
        } else {
          goodsOptionValueBox.style.margin = this.scale(1) + 'px';
          goodsOptionValueBox.style.border = this.scale(1) + 'px dashed #d41c44'
        }
      },
      /**
       *  
       * @param goodsOptionValueBoxId id
       * @param goodsOptionValueSelectedBoxId id
       * @param goodsOptionValueImgId id
       * @param clickedOptionRowIndex 
       * @param clickedOptionColIndex 
       */
      deleteStyle(goodsOptionValueBoxId, goodsOptionValueSelectedBoxId, goodsOptionValueImgId, rowIndex, colIndex) {
        let goodsOptionValueBox = document.querySelector('#' + goodsOptionValueBoxId + rowIndex + colIndex);
        //
        let goodsOptionValueSelectedBox = document.querySelector('#' + goodsOptionValueSelectedBoxId + rowIndex + colIndex);
        goodsOptionValueBox.style.color = 'black';
        goodsOptionValueBox.style.cursor = 'pointer';
        goodsOptionValueBox.style.backgroundColor = null;
        goodsOptionValueBox.style.opacity = null;
        goodsOptionValueBox.dataset.inexistence = 'false';
        goodsOptionValueSelectedBox.style.visibility = 'hidden';
        if (this.initIsStockNumberEnough(rowIndex, this.goodsOptionValues[rowIndex][colIndex])) {
          goodsOptionValueBox.style.margin = this.scale(1) + 'px';
          goodsOptionValueBox.style.border = this.scale(1) + 'px solid #ccc'
        } else {
          goodsOptionValueBox.style.margin = this.scale(1) + 'px';
          goodsOptionValueBox.style.border = this.scale(1) + 'px dashed #ccc'
        }
        let goodsOptionValueImg = document.querySelector('#' + goodsOptionValueImgId + rowIndex + colIndex);
        if (goodsOptionValueImg != null)
          goodsOptionValueImg.style.opacity = null;
      },
      /**
       * 
       * @param userSelectedGoodsOptions 
       * @returns {Array} 
       */
      computeMatchSelectedGoods(userSelectedGoodsOptions) {
        let matchSelectedGoods = [];// 
        // 
        this.goodsChild.forEach((goods, index, array) => {// 
          let flag = true;// 
          for (let i = 0; i < this.userSelectedGoodsOptions.length; i++) {// 
            if (userSelectedGoodsOptions[i].goodsOptionValue != null) {// 
              let goodsOptionValue = this.goodsOptionValues[i][userSelectedGoodsOptions[i].goodsOptionValue].text
              if (goodsOptionValue != goods['optionValue' + i]) {// 
                flag = false;
                break;
              }
            }
          }
          if (flag) {
            matchSelectedGoods.push(goods);
          }
        })
        return matchSelectedGoods;
      },
      /**
       * 
       * @param matchSelectedGoods 
       * @returns {Array} 
       */
      computeMatchSelectedGoodsOptionValues(matchSelectedGoods) {
        let matchSelectedGoodsOptionValues = [];// 
        // matchSelectedGoodsmatchSelectedGoodsOptionValues
        for (let i = 0; i < this.userSelectedGoodsOptions.length; i++) {// matchSelectedGoods
          matchSelectedGoodsOptionValues.push([]);
          matchSelectedGoods.forEach((value, index, array) => {// i
            matchSelectedGoodsOptionValues[i].push({text: null, imgSrc: null})
            // matchSelectedGoodsOptionValues[i]ii
            if (matchSelectedGoodsOptionValues[i].every(matchSelectedGoodsOptionValue => matchSelectedGoodsOptionValue.text != value['optionValue' + i])) {
              if (i != this.goodsParent.imgOptionIndex) {
                matchSelectedGoodsOptionValues[i][index].text = value['optionValue' + i]
              } else {
                matchSelectedGoodsOptionValues[i][index].text = value['optionValue' + i]
                matchSelectedGoodsOptionValues[i][index].imgSrc = value.imgSrc
              }
            }
          })
          // 
          matchSelectedGoodsOptionValues[i] = matchSelectedGoodsOptionValues[i].filter((value, index, array) => {
              return value.text != '' && value.text != null
            }
          )
        }
        return matchSelectedGoodsOptionValues;
      },
      /**
       * ''
       * @param goodsOptionValueBoxId id
       * @param goodsOptionValueImgId id
       * @param clickedOptionRowIndex 
       */
      addInexistenceStyle(goodsOptionValueBoxId, goodsOptionValueImgId, clickedOptionRowIndex) {
        let matchSelectedGoods = this.computeMatchSelectedGoods(this.userSelectedGoodsOptions)
        // 
        this.maxQuantity = 0;
        matchSelectedGoods.forEach((value, index, array) => {
          this.maxQuantity += value.stockNumber;
        })
        let matchSelectedGoodsOptionValues = this.computeMatchSelectedGoodsOptionValues(matchSelectedGoods);
        // this.previousSelectedChangedGoodsOptions[clickedOptionRowIndex]
        this.goodsOptionValues.forEach((rowValue, rowIndex) => {
          if (this.userSelectedGoodsOptions[rowIndex].goodsOptionValue == null) {// rowIndex
            rowValue.forEach((colValue, colIndex) => {
              let includes = matchSelectedGoodsOptionValues[rowIndex].some(value => {
                if (value.text == colValue.text && value.imgSrc == colValue.imgSrc) {
                  return true;
                }
              });
              // this.goodsOptionValues
              if (!includes) {
                // ''
                let goodsOptionValueChangedBox = document.querySelector('#' + goodsOptionValueBoxId + rowIndex + colIndex);
                goodsOptionValueChangedBox.style.cursor = 'not-allowed';
                goodsOptionValueChangedBox.dataset.inexistence = 'true';
                goodsOptionValueChangedBox.style.color = '#b5b5b5';
                goodsOptionValueChangedBox.style.margin = this.scale(1) + 'px';
                goodsOptionValueChangedBox.style.border = this.scale(1) + 'px dashed #ccc'
                let goodsOptionValueImg = document.querySelector('#' + goodsOptionValueImgId + rowIndex + colIndex);
                if (goodsOptionValueImg != null)
                  goodsOptionValueImg.style.opacity = 0.6;// ,
                goodsOptionValueChangedBox.style.backgroundColor = '#f3f3f3';
              }
            })
          }
          if (this.userSelectedGoodsOptions[rowIndex].goodsOptionValue != null) {// rowIndex
            let userSelectedGoodsOptions = JSON.parse(JSON.stringify(this.userSelectedGoodsOptions))// 
            userSelectedGoodsOptions[rowIndex].goodsOptionValue = null;
            let matchSelectedGoods = this.computeMatchSelectedGoods(userSelectedGoodsOptions)
            let matchSelectedGoodsOptionValues = this.computeMatchSelectedGoodsOptionValues(matchSelectedGoods);
            if (userSelectedGoodsOptions[rowIndex].goodsOptionValue == null) {// rowIndex
              rowValue.forEach((colValue, colIndex) => {
                let includes = matchSelectedGoodsOptionValues[rowIndex].some(value => {
                  if (value.text == colValue.text && value.imgSrc == colValue.imgSrc) {
                    return true;
                  }
                });
                // this.goodsOptionValues
                if (!includes) {
                  // ''
                  let goodsOptionValueChangedBox = document.querySelector('#' + goodsOptionValueBoxId + rowIndex + colIndex);
                  goodsOptionValueChangedBox.style.cursor = 'not-allowed';
                  goodsOptionValueChangedBox.dataset.inexistence = 'true';
                  goodsOptionValueChangedBox.style.color = '#b5b5b5';
                  goodsOptionValueChangedBox.style.margin = this.scale(1) + 'px';
                  goodsOptionValueChangedBox.style.border = this.scale(1) + 'px dashed #ccc'
                  let goodsOptionValueImg = document.querySelector('#' + goodsOptionValueImgId + rowIndex + colIndex);
                  if (goodsOptionValueImg != null)
                    goodsOptionValueImg.style.opacity = 0.6;// ,
                  goodsOptionValueChangedBox.style.backgroundColor = '#f3f3f3';
                }
              })
            }
          }
        })
      },

      /**
       * 
       *
       */
      setBuyBtnAndShopcartStyle(clickedOptionRowIndex, clickedOptionColIndex) {
        // 
        if (this.isUserSelectedGoodsOptionStockNumberEnough()) {
          this.isStockNumberNotEnough = false;
          // 
          this.buyBtnText = ""
          this.style.purchase.buyBtn.box.border = this.scale(2) + 'px solid #d41c44';
          this.style.purchase.buyBtn.box.backgroundColor = '#ffeced';
          this.style.purchase.buyBtn.box.cursor = 'pointer';
          this.style.purchase.buyBtn.text.color = '#d41c44';
          // 
          this.shopCartBtnImgSrc = require('../../assets/goods/shop-cart.png')
          this.shopCartBtnText = ""
          this.style.purchase.shopCartBtn.box.backgroundColor = '#e31436';
          this.style.purchase.shopCartBtn.box.border = this.scale(1) + 'px solid #e31436'
          this.style.purchase.shopCartBtn.text.color = 'white';
        } else {
          this.isStockNumberNotEnough = true;
          // 
          this.buyBtnText = ""
          this.style.purchase.buyBtn.box.border = this.scale(2) + 'px solid #ddd';
          this.style.purchase.buyBtn.box.backgroundColor = '#ddd';
          this.style.purchase.buyBtn.box.cursor = null
          this.style.purchase.buyBtn.text.color = 'white';
          // 
          this.shopCartBtnImgSrc = require('../../assets/goods/phone.png')
          this.shopCartBtnText = ""
          this.style.purchase.shopCartBtn.box.backgroundColor = '#fff';
          this.style.purchase.shopCartBtn.box.border = this.scale(1) + 'px solid #ddd'
          this.style.purchase.shopCartBtn.text.color = 'black';
        }
      },

      /**
       * 
       **/
      addStockNumberNotEnoughStyle(goodsOptionValueBoxId, clickedOptionRowIndex) {
        this.goodsOptionValues.forEach((rowValue, rowIndex) => {
          rowValue.forEach((colValue, colIndex) => {
            if (this.goodsOptionValues[rowIndex][this.userSelectedGoodsOptions[rowIndex].goodsOptionValue] != colValue) { // 
              let isStockNumberEnough = this.isStockNumberEnoughWithGoodsOptionValue(rowIndex, colValue);
              // this.goodsOptionValues
              if (!isStockNumberEnough) {
                let goodsOptionValueChangedBox = document.querySelector('#' + goodsOptionValueBoxId + rowIndex + colIndex);
                goodsOptionValueChangedBox.style.color = '#b5b5b5';
                goodsOptionValueChangedBox.style.margin = this.scale(1) + 'px';
                goodsOptionValueChangedBox.style.border = this.scale(1) + 'px dashed #dedede';
                goodsOptionValueChangedBox.style.opacity = 0.5;
              }
            }
          })
        })
      },

      /**
       * 
       * @param event 
       * @param goodsOptionValueBoxId id
       * @param goodsOptionValueSelectedBoxId id
       * @param goodsOptionValueImgId id
       * @param clickedOptionRowIndex 
       * @param clickedOptionColIndex 
       */
      goodsOptionValueBoxClick(event, goodsOptionValueBoxId, goodsOptionValueSelectedBoxId, goodsOptionValueImgId, clickedOptionRowIndex, clickedOptionColIndex) {
        let goodsOptionValueBox = document.querySelector('#' + goodsOptionValueBoxId + clickedOptionRowIndex + clickedOptionColIndex);
        if (goodsOptionValueBox.dataset.inexistence == 'true')// 
          return;
        if (clickedOptionRowIndex == this.goodsParent.imgOptionIndex) {// 
          this.originalImgSrc = this.goodsOptionValues[clickedOptionRowIndex][clickedOptionColIndex].imgSrc;// 
        }
        if (this.userSelectedGoodsOptions[clickedOptionRowIndex].goodsOptionValue == clickedOptionColIndex) {// 
          if (clickedOptionRowIndex == this.goodsParent.imgOptionIndex) {
            this.userSelectedImgOptionValue = null;
          }
          this.userSelectedGoodsOptions[clickedOptionRowIndex].goodsOptionValue = null;
          this.setBuyBtnAndShopcartStyle(clickedOptionRowIndex, clickedOptionColIndex);
        } else {// 
          if (clickedOptionRowIndex == this.goodsParent.imgOptionIndex) {
            this.userSelectedImgOptionValue = this.goodsOptionValues[clickedOptionRowIndex][clickedOptionColIndex].text;
          }
          this.userSelectedGoodsOptions[clickedOptionRowIndex].goodsOptionValue = clickedOptionColIndex;
          this.setBuyBtnAndShopcartStyle(clickedOptionRowIndex, clickedOptionColIndex);
        }
        // 
        this.goodsOptionValues.forEach((rowValue, rowIndex) => {
          rowValue.forEach((colValue, colIndex) => {
            this.deleteStyle(goodsOptionValueBoxId, goodsOptionValueSelectedBoxId, goodsOptionValueImgId, rowIndex, colIndex)
          })
        })
        // ''
        this.addInexistenceStyle(goodsOptionValueBoxId, goodsOptionValueImgId, clickedOptionRowIndex)
        // 
        this.addStockNumberNotEnoughStyle(goodsOptionValueBoxId, clickedOptionRowIndex)
        // 
        this.userSelectedGoodsOptions.forEach((userSelectedGoodsOption, index) => {
          if (userSelectedGoodsOption.goodsOptionValue != null) {
            this.addClickedStyle(goodsOptionValueBoxId, goodsOptionValueSelectedBoxId, index, userSelectedGoodsOption.goodsOptionValue)
          }
        })
      },
      /**
       * 'hover'
       * @param event 
       * @param goodsOptionValue 
       * @param enterOptionRowIndex 'hover'
       */
      goodsOptionValueBoxEnter(event, goodsOptionValue, enterOptionRowIndex) {
        let style = this.getEventElementStyle(event);
        // 
        if (this.goodsOptionValues[enterOptionRowIndex][this.userSelectedGoodsOptions[enterOptionRowIndex].goodsOptionValue] != goodsOptionValue && event.currentTarget.dataset.inexistence == 'false') {
          style.color = '#d41c44';
          if (this.isStockNumberEnoughWithGoodsOptionValue(enterOptionRowIndex, goodsOptionValue)) {
            style.margin = this.scale(0) + 'px';
            style.border = this.scale(2) + 'px solid #d41c44'
          } else {
            style.margin = this.scale(1) + 'px';
            style.border = this.scale(1) + 'px dashed #d41c44'
          }
        }
      }
      ,
      /**
       * 
       * @param event 
       * @param goodsOptionValue 
       * @param enterOptionRowIndex 
       */
      goodsOptionValueBoxLeave(event, goodsOptionValue, enterOptionRowIndex) {
        let style = this.getEventElementStyle(event);
        // ,
        if (this.goodsOptionValues[enterOptionRowIndex][this.userSelectedGoodsOptions[enterOptionRowIndex].goodsOptionValue] != goodsOptionValue && event.currentTarget.dataset.inexistence == 'false') {
          style.margin = this.scale(1) + 'px';
          if (this.isStockNumberEnoughWithGoodsOptionValue(enterOptionRowIndex, goodsOptionValue)) {
            style.color = 'black';
            style.border = this.scale(1) + 'px solid #ccc'
          } else {
            style.color = '#b5b5b5';
            style.border = this.scale(1) + 'px dashed #ccc'
          }
        }
      },

      /**
       * 
       * @param goodsOptionRowIndex 
       * @param goodsOptionValue 
       * @returns {boolean} >0,true;<0,false
       */
      initIsStockNumberEnough(goodsOptionRowIndex, goodsOptionValue) {
        // 1.
        let matchGoodsChild = this.goodsChild.filter((goodsChildItem) => {
          // 
          if (goodsChildItem['optionValue' + goodsOptionRowIndex] == goodsOptionValue.text) {
            return true;
          } else {
            return false;
          }
        })
        // 2.
        let isStockNumberEnough = matchGoodsChild.some((matchGoodsChildItem, index, array) => {
          return matchGoodsChildItem.stockNumber > 0 // >0
        })
        return isStockNumberEnough;
      },

      /**
       * 
       * @param goodsOptionRowIndex 
       * @param goodsOptionValue 
       * @returns {boolean} >0,true;<0,false
       */
      isStockNumberEnoughWithGoodsOptionValue(goodsOptionRowIndex, goodsOptionValue) {
        // 1.
        let containGoodsOptionValueGoodsChild = this.goodsChild.filter((goodsChildItem) => {
          // 
          if (goodsChildItem['optionValue' + goodsOptionRowIndex] == goodsOptionValue.text) {
            return true;
          } else {
            return false;
          }
        })
        // 2.containGoodsOptionValueGoodsChild
        let matchGoodsChild = containGoodsOptionValueGoodsChild.filter((goodsChildItem) => {
          let contain = true;// 
          for (let i = 0; i < this.userSelectedGoodsOptions.length; i++) {
            if (i != goodsOptionRowIndex) {// 
              if (this.userSelectedGoodsOptions[i].goodsOptionValue !== null) {// 
                if (this.goodsOptionValues[i][this.userSelectedGoodsOptions[i].goodsOptionValue].text != goodsChildItem['optionValue' + i]) {// 
                  contain = false;// 
                  break;
                }
              }
            }
          }
          if (contain) {
            return true;
          } else {
            return false;
          }
        })
        // 3.
        let isStockNumberEnough = matchGoodsChild.some((matchGoodsChildItem, index, array) => {
          return matchGoodsChildItem.stockNumber > 0 // >0
        })
        return isStockNumberEnough;
      },

      /**
       * 
       * @returns {boolean} >0,true;<0,false
       */
      isUserSelectedGoodsOptionStockNumberEnough() {
        // 1.containGoodsOptionValueGoodsChild
        let matchGoodsChild = this.goodsChild.filter((goodsChildItem) => {
          let contain = true;// 
          for (let i = 0; i < this.userSelectedGoodsOptions.length; i++) {
            if (this.userSelectedGoodsOptions[i].goodsOptionValue !== null) {// 
              if (this.goodsOptionValues[i][this.userSelectedGoodsOptions[i].goodsOptionValue].text != goodsChildItem['optionValue' + i]) {// 
                contain = false;// 
                break;
              }
            }
          }
          if (contain) {
            return true;
          } else {
            return false;
          }
        })
        // 2.
        let isStockNumberEnough = matchGoodsChild.some((matchGoodsChildItem, index, array) => {
          return matchGoodsChildItem.stockNumber > 0 // >0
        })
        return isStockNumberEnough;
      },

      /**
       * 
       * @param goodsOptionValue 
       * @param goodsOptionRowIndex 
       * @returns {default.computed.goodsOptionValueTextBoxOnStockNumberNotEnough|(function())|default.computed.goodsOptionValueImgBoxOnStockNumberEnough|default.computed.goodsOptionValueImgBoxOnStockNumberNotEnough|default.computed.goodsOptionValueTextBoxOnStockNumberEnough}
       */
      setGoodsOptionValueBoxStyle(goodsOptionValue, goodsOptionRowIndex) {
        let goodsOptionValueBoxStyle = {};
        if (goodsOptionRowIndex == this.goodsParent.imgOptionIndex) {// 
          if (this.initIsStockNumberEnough(goodsOptionRowIndex, goodsOptionValue)) {
            goodsOptionValueBoxStyle = this.goodsOptionValueImgBoxOnStockNumberEnough;
          } else {
            goodsOptionValueBoxStyle = this.goodsOptionValueImgBoxOnStockNumberNotEnough;
          }
        } else {
          if (this.initIsStockNumberEnough(goodsOptionRowIndex, goodsOptionValue)) {
            goodsOptionValueBoxStyle = this.goodsOptionValueTextBoxOnStockNumberEnough;
          } else {
            goodsOptionValueBoxStyle = this.goodsOptionValueTextBoxOnStockNumberNotEnough;
          }
        }
        return goodsOptionValueBoxStyle;
      }
      ,
      /**
       * 
       * @param event
       */
      goodsExplanationEnter(event) {
        let style = this.getEventElementStyle(event);
        style.textDecoration = 'underline'
        style.cursor = 'pointer'
      }
      ,
      /**
       * 
       * @param event
       */
      goodsExplanationLeave(event) {
        let style = this.getEventElementStyle(event);
        style.textDecoration = 'none'
      }
      ,
      /**
       * 
       * @param href 
       */
      goodsExplanationClick(href) {
        // 
      }
      ,
      /**
       * 
       * @param event
       * @param buyBtnId
       */
      buyBtnEnter(event, buyBtnId) {
        if (!this.isStockNumberNotEnough) {
          let style = this.getEventElementStyle(event);
          style.border = this.scale(2) + 'px solid #f8305b';
          style.backgroundColor = 'white';
          let buyBtn = document.querySelector('#' + buyBtnId);
          buyBtn.childNodes[0].style.color = '#f8305b';
        }
      }
      ,
      /**
       * 
       * @param event
       * @param buyBtnId
       */
      buyBtnLeave(event, buyBtnId) {
        if (!this.isStockNumberNotEnough) {
          let style = this.getEventElementStyle(event);
          style.border = this.scale(2) + 'px solid #d41c44';
          style.backgroundColor = '#ffeced';
          let buyBtn = document.querySelector('#' + buyBtnId);
          buyBtn.childNodes[0].style.color = '#d41c44';
        }
      }
      ,
      /**
       * 
       */
      buyBtnClick() {
      }
      ,
      /**
       * ''
       * @param event
       */
      shopCartBtnEnter(event) {
        if (!this.isStockNumberNotEnough) {
          let style = this.getEventElementStyle(event);
          style.backgroundColor = '#f8305b';
        }
      }
      ,
      /**
       * ''
       * @param event
       */
      shopCartBtnLeave(event) {
        if (!this.isStockNumberNotEnough) {
          let style = this.getEventElementStyle(event);
          style.backgroundColor = '#e31436';
        }
      }
      ,
      /**
       * ''
       */
      shopCartBtnClick() {
      }
      ,
      /**
       * 
       */
      collectBtnClick() {
      }
      ,
      /**
       * goodsChildoptionValue
       */
      generateGoodsOptionValues() {
        for (let i = 0; i < this.userSelectedGoodsOptions.length; i++) {
          this.goodsOptionValues.push([]);
          this.previousSelectedChangedGoodsOptions.push({changedGoodsOptions: []});
          this.previousSelectedGoodsOptions.push({colIndex: null});
          this.goodsChild.forEach((value, index, array) => {
            this.goodsOptionValues[i].push({text: null, imgSrc: null})
            if (this.goodsOptionValues[i].every(goodsOptionValue => goodsOptionValue.text != value['optionValue' + i])) {
              if (i != this.goodsParent.imgOptionIndex) {
                this.goodsOptionValues[i][index].text = value['optionValue' + i]
              } else {
                this.goodsOptionValues[i][index].text = value['optionValue' + i]
                this.goodsOptionValues[i][index].imgSrc = value.imgSrc
              }
            }
          })
          // optionValue
          this.goodsOptionValues[i] = this.goodsOptionValues[i].filter((value, index, array) => {
              return value.text != '' && value.text != null
            }
          )
        }
        this.computeMaxQuantity();
      },

      computeMaxQuantity() {
        this.goodsChild.forEach((value, index, array) => {
          this.maxQuantity += value.stockNumber;
        })
      },

      getGoodsParent() {
        this.$page.get('goods/getGoodsDetailGoodsParent', {id: this.goodsParentId}, null,
          response => {
            this.goodsParent = response.data;
            this.originalImgSrc = this.goodsParent.imgSrc;
            this.getGoodsChild();
          },
          error => {
            this.goodsParent = null;
            this.userSelectedGoodsOptions = null;
          })
      }
      ,

      getUserSelectedGoodsOptions() {
        this.$page.get('goods/getUserSelectedGoodsOptions', {goodsParentId: this.goodsParentId}, null,
          response => {
            this.userSelectedGoodsOptions = response.data;
            this.generateGoodsOptionValues();
          },
          error => {
            this.computeMaxQuantity();
            this.userSelectedGoodsOptions = null;
          })
      }
      ,

      getGoodsChild() {
        this.$page.get("/goods/getGoodsChild", {goodsParentId: this.goodsParentId}, null,
          response => {
            this.goodsChild = response.data;
            //this.generateGoodsOptionValues();
            this.getUserSelectedGoodsOptions();
          })
      },

      getGoodsExplanation() {
        this.$page.get("/goods/getGoodsExplanation", {goodsParentId: this.goodsParentId}, null,
          response => {
            this.goodsExplanations = response.data;
          })
      },

    },
    beforeMount() {
      this.getGoodsParent();
      this.getGoodsExplanation();
    }
  }
</script>

<style scoped>
  dl, dt, dd, ul, ol, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }
</style>



